body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.item {
  border: 1px dashed grey;
  padding: 4px;
  user-select: none;
}

.item:hover {
  background: rgba(0, 0, 0, 0.1);
}

ul {
  display: flex;
  flex-direction: column;
  gap: 4px;
  max-width: 200px;
  font-size: 1.2rem;
}
